@charset "UTF-8";
.investor-banner {
  z-index: 1;
  width: 100%;
  margin-top: 160px; }
  .investor-banner #banner-img {
    width: 100%; }
  .investor-banner .bottom-container {
    opacity: 0;
    transition: 1s;
    width: 988px;
    position: relative;
    margin: 0 auto; }
    .investor-banner .bottom-container .bottom-item {
      color: white;
      position: absolute;
      width: 988px;
      height: 160px;
      left: 0;
      background-color: purple;
      margin-top: -80px;
      z-index: 12; }
      .investor-banner .bottom-container .bottom-item .title {
        margin: 38px 0px 38px 130px; }
        .investor-banner .bottom-container .bottom-item .title h3 {
          font-size: 32px;
          margin-bottom: 15px; }
        .investor-banner .bottom-container .bottom-item .title h4 {
          font-size: 16px;
          margin-bottom: 12px;
          color: #ff5bf9; }
        .investor-banner .bottom-container .bottom-item .title h5 {
          font-size: 18px; }
    .investor-banner .bottom-container .bottom-item-right {
      position: absolute;
      background-color: hotpink;
      width: 90px;
      height: 160px;
      right: 0;
      margin-top: -80px;
      z-index: 13; }
      .investor-banner .bottom-container .bottom-item-right #bottom-icon {
        margin: 66px 38px; }

.investor-images {
  width: 1200px;
  margin: 150px auto 0px; }
  .investor-images .investor-category {
    position: relative;
    margin-bottom: 60px;
    width: 100%;
    height: 460px; }
    .investor-images .investor-category a {
      text-decoration: none; }
    .investor-images .investor-category .investor-category-left {
      margin-top: 80px;
      width: 302px;
      height: 302px;
      position: absolute; }
    .investor-images .investor-category .investor-category-right {
      position: absolute;
      background-color: white;
      opacity: 0.95;
      top: 30px;
      right: 30px;
      width: 440px;
      height: 400px; }
      .investor-images .investor-category .investor-category-right a {
        text-decoration: none; }
      .investor-images .investor-category .investor-category-right:hover hr {
        width: 200px; }
      .investor-images .investor-category .investor-category-right:hover h3 {
        transform: scale(1.04, 1.04);
        -ms-transform: scale(1.04, 1.04);
        -moz-transform: scale(1.04, 1.04);
        -webkit-transform: scale(1.04, 1.04);
        -o-transform: scale(1.04, 1.04); }
        .investor-images .investor-category .investor-category-right:hover h3 img {
          transform: rotate(360deg);
          -ms-transform: rotate(360deg);
          /* IE 9 */
          -moz-transform: rotate(360deg);
          /* Firefox */
          -webkit-transform: rotate(360deg);
          /* Safari 和 Chrome */
          -o-transform: rotate(360deg); }
      .investor-images .investor-category .investor-category-right .investor-category-bt {
        margin: 54px 96px 12px 60px; }
      .investor-images .investor-category .investor-category-right hr {
        margin: 10px 0px 70px 60px;
        transition: all 1s;
        width: 100px;
        height: 3px;
        background-color: black; }
      .investor-images .investor-category .investor-category-right h3 {
        font-size: 36px;
        color: #333333;
        transition: all 0.5s;
        margin: 0px 0px 16px 60px; }
        .investor-images .investor-category .investor-category-right h3 img {
          transition: all 0.5s;
          margin-left: 17px; }
      .investor-images .investor-category .investor-category-right p {
        width: 310px;
        margin-left: 60px;
        font-size: 14px;
        color: #666666; }
