@charset "UTF-8";
.activity-banner {
  width: 100%;
  margin-top: 160px; }
  .activity-banner img {
    width: 100%; }

.activity-choice {
  width: 1250px;
  height: 1400px;
  margin: 0 auto; }
  .activity-choice .activity-choice-nav {
    position: relative; }
    .activity-choice .activity-choice-nav ul {
      display: flex;
      margin-left: 240px; }
      .activity-choice .activity-choice-nav ul li {
        transition: all 1s;
        -moz-transition: all 0.5s;
        /* Firefox 4 */
        -webkit-transition: all 0.5s;
        /* Safari and Chrome */
        -o-transition: all 0.5s;
        padding: 10px;
        border-radius: 20px;
        font-size: 16px;
        margin-right: 60px;
        list-style: none; }
        .activity-choice .activity-choice-nav ul li a {
          color: #333333;
          text-decoration: none; }
        .activity-choice .activity-choice-nav ul li:hover {
          background-color: #5B2580;
          color: white; }
      .activity-choice .activity-choice-nav ul .search {
        border-radius: 30px;
        overflow: hidden;
        height: 45px;
        width: 275px;
        position: relative; }
        .activity-choice .activity-choice-nav ul .search img {
          margin-left: 5px;
          width: 16px;
          display: inline-block; }
        .activity-choice .activity-choice-nav ul .search .search-input {
          display: inline-block; }
          .activity-choice .activity-choice-nav ul .search .search-input input {
            border: none;
            color: black;
            position: absolute;
            left: 60px;
            width: 100px;
            background-color: #00b3ee; }
          .activity-choice .activity-choice-nav ul .search .search-input input:focus {
            outline: none; }
          .activity-choice .activity-choice-nav ul .search .search-input .serach-btn {
            display: inline-block;
            width: 20px; }
        .activity-choice .activity-choice-nav ul .search:hover {
          background-color: white;
          color: white; }
  .activity-choice .activity-choice-items .activity-choice-item {
    box-shadow: 0 0 3px #e8e6e6;
    border: 1px solid #e8e6e6;
    width: 280px;
    height: 310px;
    display: inline-block;
    margin: 50px 14px 0px;
    transition: all 0.5s;
    -moz-transition: all 0.5s;
    /* Firefox 4 */
    -webkit-transition: all 0.5s;
    /* Safari and Chrome */
    -o-transition: all 0.5s; }
    .activity-choice .activity-choice-items .activity-choice-item a {
      text-decoration: none; }
    .activity-choice .activity-choice-items .activity-choice-item .item-pic {
      overflow: hidden;
      width: 280px;
      height: 230px; }
      .activity-choice .activity-choice-items .activity-choice-item .item-pic img {
        transition: all 1s;
        -moz-transition: all 1s;
        /* Firefox 4 */
        -webkit-transition: all 1s;
        /* Safari and Chrome */
        -o-transition: all 1s;
        width: 280px;
        height: 230px; }
    .activity-choice .activity-choice-items .activity-choice-item h3 {
      font-family: "微软雅黑";
      margin-top: 12px;
      text-align: center;
      font-size: 16px;
      color: #333333;
      margin-bottom: 10px;
      font-weight: 100; }
    .activity-choice .activity-choice-items .activity-choice-item .item-line {
      transition: all 1s;
      -moz-transition: all 1s;
      /* Firefox 4 */
      -webkit-transition: all 1s;
      /* Safari and Chrome */
      -o-transition: all 1s;
      background-color: #00b3ee;
      width: 0%;
      margin: 0 auto;
      height: 3px; }
    .activity-choice .activity-choice-items .activity-choice-item h4 {
      font-family: "微软雅黑";
      text-align: center;
      font-size: 12px;
      color: #666666; }
      .activity-choice .activity-choice-items .activity-choice-item h4 span {
        margin-left: 40px; }
    .activity-choice .activity-choice-items .activity-choice-item:hover {
      box-shadow: 3px 3px 3px #e8e6e6; }
      .activity-choice .activity-choice-items .activity-choice-item:hover .item-pic img {
        -webkit-transform: scale(1.1, 1.1);
        -moz-transform: scale(1.1, 1.1);
        -transform: scale(1.1, 1.1); }
      .activity-choice .activity-choice-items .activity-choice-item:hover .item-line {
        width: 30%; }
